import React from "react";
import CoursePage from "../../Components/Course/CoursePage";
import { key_features_yoga } from "../../Constants/courses";

const Yoga = () => {
  return (
    <>
      <CoursePage
        titleOne="Yoga"
        url="../images/yoga.png"
        url2="../images/yoga2.jpg"
        courseTitle="Yoga - Discovering Harmony Within"
        courseDesc="Welcome to the world of Yoga, where ancient wisdom meets modern living. Whether you're a beginner seeking solace or an experienced yogi in pursuit of deeper insights, our Yoga course offers a holistic approach to harmonizing your physical, mental, and spiritual aspects. Embark on this transformative journey of self-discovery and unlock the power of Yoga in your life. Whether you're seeking physical rejuvenation, mental clarity, or spiritual growth, our Yoga course offers a path to inner harmony and personal transformation."
        keyFeaturesData={key_features_yoga}
        instructor_img="../images/profile3.jpg"
        name="John"
        designation="Yoga Guru"
        yoe="5+"
      />
    </>
  );
};

export default Yoga;
